@charset "utf-8";

/* 웹폰트 */
@include webFont(Noto Sans KR, notosanskr, notosanskr100, 100);
@include webFont(Noto Sans KR, notosanskr, notosanskr300, 300);
@include webFont(Noto Sans KR, notosanskr, notosanskr350, 350);
@include webFont(Noto Sans KR, notosanskr, notosanskrregular, 400);
@include webFont(Noto Sans KR, notosanskr, notosanskr500, 500);
@include webFont(Noto Sans KR, notosanskr, notosanskr700, 700);
@include webFont(Noto Sans KR, notosanskr, notosanskr900, 900);
// @include webFont(Roboto, roboto, roboto100, 100);
// @include webFont(Roboto, roboto, roboto, 400);
// @include webFont(Roboto, roboto, roboto500, 500);
@include webFont2(NanumGothicWeb, nanumgothic, NanumGothic, 400);

// basic
html {
    scroll-behavior: smooth;
}

body {
    overflow-x: auto;
    overflow-y: hidden;
    min-width: 1920px;
    font-family: "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    letter-spacing: -1px;

    @include wordwrap;
}

// common class
.ir {
    overflow: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    font-size: 1%;
    z-index: -9999;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

.is-hidden {
    display: none;
}

#skip {
    a {
        position: absolute;
        left: 0;
        top: -30px;
    }
}

// header
.header {
    position: relative;
    background-color: #2b2c30;
    z-index: 99;
    
    @include box-shadow(0 0px 5px 0 rgba(0, 0, 0, 0.6));

    .header-wrap {
        position: relative;
        max-width: 1920px;
        height: 60px;
        // margin: 0 auto;
        padding: 0 20px;

        @include box-sizing;
    }

    .ci {
        padding: 12px 0 11px;

        img {
            vertical-align: top;
        }
    }

    .title {
        position: absolute;
        left: 50%;
        top: 50%;
        font-family: NanumGothicWeb;
        font-size: 20px;
        color: #ccc;
        font-weight: bold;
        text-align: center;

        @include translate(-50%, -50%);
    }

    .util {
        position: absolute;
        right: 20px;
        top: 50%;
        
        @include flexbox;
        @include align-items(center);
        @include translate(0, -50%);
        
        .date {
            margin-right: 10px;
            font-family: NanumGothicWeb;
            font-size: 12px;
            color: #999;
            letter-spacing: 0;
        }

        .btn-logout {
            width: 62px;
            height: 22px;
            border: 0;
            font-family: NanumGothicWeb;
            font-size: 12px;
            color: #fff;
            font-weight: bold;
            background-color: #a57d28;

            @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.5));
            @include border-radius(3px);
        }
    }
}

//gnb
.gnb {
    position: fixed;
    top: 60px;
    left: 0;
    width: 70px;
    height: calc(100% - 60px);
    padding-top: 30px;
    background-color: #212124;
    text-align: center;
    z-index: 9;

    @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75)); 
    @include box-sizing;   
    
    .btn-gnb {
        display: block;
        height: 80px;
        padding-top: 12px;
        font-size: 14px;
        color: #999;
        font-weight: 700;
        text-align: center;

        @include box-sizing;

        &:before {
            content: "";
            display: block;
            margin: 0 auto;

            @include size(39px);
        }

        &.btn-map {
            &:before {
                background: url(../images/comm/ico_map.png) no-repeat 50% 50%;
            }
        }

        &.btn-graph {
            &:before {
                background: url(../images/comm/ico_graph.png) no-repeat 50% 50%;
            }
        }

        &.btn-manage {
            &:before {
                background: url(../images/comm/ico_gear.png) no-repeat 50% 50%;
            }
        }
    }

    li {
        &.is-active,
        &:hover {
            .btn-gnb {
                color: #f6c150;
                background-color: #151516;

                &.btn-map {
                    &:before {
                        background-image: url(../images/comm/ico_map_on.png);
                    }
                }

                &.btn-graph {
                    &:before {
                        background-image: url(../images/comm/ico_graph_on.png);
                    }
                }

                &.btn-manage {
                    &:before {
                        background-image: url(../images/comm/ico_gear_on.png);
                    }
                }
            }
        }
    }
}

// 로그인
.section-login {
    min-height: 100vh;
    padding-top: calc((200 / 1920) * 100%);
    background-color: #212124;

    @include box-sizing;

    .login-wrap {
        max-width: 600px;
        margin: 0 auto;
    }

    .login-box {
        padding: 57px 100px 75px;
        background-color: #2b2c30;
        text-align: center;

        @include border-radius(5px);
        @include box-sizing;
    }

    .ico-group {
        font-size: 0;
        text-align: center;

        li {
            display: inline-block;
            position: relative;
            margin-left: 20px;
            background-color: #232428;
            vertical-align: top;

            @include size(78px);
            @include border-radius(200px);

            img {
                position: absolute;
                left: 50%;
                top: 50%;

                @include translate(-50%, -50%);
            }

            &:first-child {
                margin-left: 0;
            }
        }
    }

    .title {
        margin-top: 16px;
        font-size: 27px;
        color: #fff;
        font-weight: 400;
        text-align: center;
    }

    .login-form {
        margin: 33px 0 38px;

        .login-form-input {
            display: block;

            & + .login-form-input {
                margin-top: 20px;
            }
        }

        .input-text {
            width: 100%;
            height: 60px;
            padding: 0 30px;
            border: 0;
            color: #fff;
            background-color: #43454c;

            @include border-radius(10px);
            @include box-shadow(inset 0 1px 2px 0 rgba(0, 0, 0, 0.75));
            @include box-sizing;

            &::placeholder {
                font-size: 14px;
                color: #fff;
                letter-spacing: 0;
            }
        }

        .btn-login {
            width: 100%;
            height: 60px;
            margin-top: 60px;
            border: 0;
            font-size: 20px;
            color: #fff;
            font-weight: 500;
            background-image: -moz-linear-gradient( 90deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
            background-image: -webkit-linear-gradient( 90deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
            background-image: -ms-linear-gradient( 90deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
            background-image: linear-gradient( 90deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
            
            @include border-radius(10px);
            @include box-shadow(0 6px 10px 0 rgba(0, 0, 0, 0.2));
        }
    }

    .find-account {
        font-size: 14px;
        color: #666;
        letter-spacing: 0;
    }

    .login-footer {
        margin-top: 80px;
        text-align: center;

        .copyright {
            margin-top: 30px;
            font-size: 14px;
            color: #666;
            letter-spacing: 0;
        }
    }
} 

// lnb 
.lnb {
    position: relative;
    height: 60px;
    background-image: -moz-linear-gradient( 0deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
    background-image: -webkit-linear-gradient( 0deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
    background-image: -ms-linear-gradient( 0deg, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
    background-image: linear-gradient(left, rgb(76,168,54) 0%, rgb(15,110,129) 50%, rgb(14,65,148) 100%);
    z-index: 1;

    @include flexbox;
    @include justify-content(center);
    @include border-radius(0 0 8px 8px);
    @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75));

    .lnb-item {
        position: relative;
        margin: 0 80px;

        @include transition(background-color .3s);

        &.is-on,
        &:hover {
            background-color: rgba(255, 255, 255, .2);

            .lnb-sub {
                opacity: 1;
            }
        }

        &.is-active {
            background-color: rgba(255, 255, 255, .2);
        }
    }

    .btn-lnb {
        height: 60px;
        padding: 10px 30px;
        border: 0;
        font-size: 20px;
        color: #fff;
        font-weight: 500;
        background: none;
        text-align: center;
        text-shadow: 0 0 3px rgba(0, 0, 0, 0.5);

        @include box-sizing;
    }
    a.btn-lnb {
        display: block;
        line-height: 40px;
    }

    .lnb-sub {
        opacity: 0;
        display: none;
        position: absolute;
        top: 61px;
        left: 50%;
        min-width: 100%;
        padding: 0 5px 8px;
        background: #2c3e82;
        text-align: center;

        @include border-radius(0 0 8px 8px);
        @include box-sizing;
        @include translate(-50%, 0);
        @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75));
        @include transition(opacity .3s);

        li {
            height: 40px;
            padding: 0 20px;
            border-bottom: 1px solid #4854ad;
            white-space: nowrap;

            &:last-child {
                border-bottom: 0;
            }
        }
    }

    .btn-lnb-sub {
        display: block;
        font-size: 20px;
        color: #fff;
        font-weight: 500;
        text-align: center;
        line-height: 40px;

        @include transition(color .3s);

        &.is-active,
        &:hover {
            color: #fef362;
        }
    }
}
  
