@charset "utf-8";

// comm
[class^=section-] {
    overflow-y: auto;
    height: calc(100vh - 60px);
    background-color: #151516;
}

.gnb {
    & + [class$=-main] {
        margin-left: 70px;
    }
}

// 그래프형
.section-graph {
    .graph-wrap {
        position: relative;
        max-width: 1920px;
        // margin: 0 auto;
    }

    .graph-main {
        @include clear;
    }

    .graph-info {
        float: left;
        width: 300px;
        padding: 23px 0 0 40px;

        .graph-info-box {
            margin-top: 40px;

            &:first-child {
                margin-top: 0;
            }
        }

        h3.title {
            margin-bottom: 5px;
            font-size: 20px;
            color: #fff;;
            font-weight: 500;
        }

        .graph-info-board {
            overflow: hidden;
            width: 100%;

            @include border-radius(6px);

            th {
                height: 50px;
                font-size: 14px;
                color: #ccc;
                font-weight: 400;
                background-color: #36373c;

                span {
                    font-size: 11px;
                }
            }

            td {
                height: 29px;
                padding: 5px;
                border-top: 1px solid #151516;
                font-family: NanumGothicWeb;
                font-size: 14px;
                color: #fff;
                background-color: #2b2c30;
                letter-spacing: 0;
                text-align: center;
            }

            .tit {
                font-family: "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
                color: #ccc;

                span {
                    font-size: 11px;
                }
            }
        }

        .graph-info-use {
            .title {
                position: relative;
                padding-left: 37px;
                line-height: 30px;

                &:before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 50%;
                    margin-top: -15px;
                    background: #2b2c30 url(../images/comm/ico_thunder_medium.png) no-repeat 50% 50%;

                    @include size(30px);
                    @include border-radius(100px);
                }
            }
        }

        .graph-info-price {
            margin-top: 20px;

            h4.title {
                margin-bottom: 10px;
                font-size: 18px;
                color: #fef698;
                font-weight: 500;
            }
        }

        .graph-info-dust {
            .title {
                position: relative;
                padding-left: 37px;
                line-height: 30px;

                &:before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 50%;
                    margin-top: -15px;
                    background: #2b2c30 url(../images/comm/ico_dust_medium.png) no-repeat 50% 50%;

                    @include size(30px);
                    @include border-radius(100px);
                }
            }

            .graph-info-board {
                table-layout: fixed;            
            }
        }
    }

    .graph-contents {
        overflow: hidden;
        padding: 30px 40px;

        @include box-sizing;

        .graph-top {
            ul {
                @include clear;
            }

            li {
                position: relative;
                float: left;
                width: calc((100% - 80px) / 5);
                min-width: 250px;
                height: 140px;
                margin-right: 20px;
                padding: 25px 30px 25px 130px;

                @include border-radius(20px);
                @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75));
                @include box-sizing;

                &:before {
                    content: "";                    
                    position: absolute;
                    left:  0;
                    top: 0;
                    width: 134px;
                    height: 100%;
                }

                &:last-child {
                    margin-right: 0;
                }

                .name {
                    font-size: 20px;
                    font-weight: 500;
                    color: #fff;
                }

                span {
                    font-size: 14px;
                    font-weight: 400;
                }

                .unit {
                    display: block;
                    margin-top: 5px;
                }

                .value {
                    display: block;
                    position: absolute;
                    right: 30px;
                    bottom: 25px;
                    font-family: NanumGothicWeb;
                    font-size: 40px;
                    color: #fff;
                    font-weight: bold;
                    text-align: right;
                }

                &.index-top-ultradust {
                    background-color: #bd5529;

                    &:before {
                        background: url(../images/ico_index_ultradust.png) no-repeat 50% 50%;;
                    }

                    span {
                        color: #f29b76;
                    }
                }

                &.index-top-dust {
                    background-color: #dd8b0e;

                    &:before {
                        background: url(../images/ico_index_dust.png) no-repeat 50% 50%;;
                    }

                    span {
                        color: #facd89;
                    }
                }

                &.index-top-ozone {
                    background-color: #7fa917;

                    &:before {
                        background: url(../images/ico_index_ozone.png) no-repeat 50% 50%;;
                    }

                    span {
                        color: #cce198;
                    }
                }

                &.index-top-temp {
                    background-color: #3777cd;

                    &:before {
                        background: url(../images/ico_index_temp.png) no-repeat 50% 50%;;
                    }

                    span {
                        color: #88abda;
                    }
                }

                &.index-top-humidity {
                    background-color: #8137ac;

                    &:before {
                        background: url(../images/ico_index_humidity.png) no-repeat 50% 50%;
                    }

                    span {
                        color: #aa89bd;
                    }
                }
            }
        }

        .graph-group {
            margin-top: 20px;

            .graph-group-box {
                overflow: hidden;
                margin-top: 20px;
                background-color: #2b2c30;

                @include border-radius(5px);
                @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75));
                @include clear;

                &:first-child {
                    margin-top: 0;
                }
            }

            .graph-group-head {
                float: left;
                width: 200px;
                padding: 35px 0;
                text-align: center;

                .title {
                    font-size: 24px;
                    color: #fff;
                    font-weight: 400;
                }

                .status {
                    margin-top: 30px;
                    
                    span {
                        display: inline-block;
                        position: relative;
                        background-color: #232428;
                        vertical-align: top;

                        @include size(80px);
                        @include border-radius(200px);

                        img {
                            position: absolute;
                            top: 50%;
                            left: 50%;

                            @include translate(-50%, -50%);
                        }
                    }
                }

                .name {
                    display: inline-block;
                    width: 90px;
                    height: 30px;
                    margin-top: 30px;
                    border: 1px solid #3c3d42;
                    font-size: 16px;
                    color: #fff;
                    vertical-align: top;
                    background-color: #232428;
                    text-align: center;
                    line-height: 28px;

                    @include box-sizing;
                    @include border-radius(3px);
                }
            }

            .graph-group-chart {
                overflow: hidden;
                float: left;
                width: calc(100% - 200px);

                @include box-sizing;
            }

            .graph-group-alarm {
                .graph-group-chart {
                    padding: 30px 70px;
                }
            }
        }
    } 
}

// 관리
.section-manage {
    .manage-wrap {
        position: relative;
        max-width: 1920px;
    }

    .manage-main {
        // overflow: hidden;
        padding: 0 40px 30px;
    }

    .manage-contents {
        position: relative;
        margin-top: 30px;

        .btn-excel-dowm {
            position: absolute;
            right: 0;
            top: 0;
        }

        .title {
            font-size: 30px;
            color: #fef698;
            font-weight: 500;
        }

        .manage-group {
            margin-top: 20px;
        }

        .manage-group-box {
            margin-top: 40px;

            .manage-group-item {
                background-color: #2b2c30;

                @include box-sizing;
                @include border-radius(5px);
                @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75));
                
                .tit {
                    position: relative;
                    height: 40px;
                    margin-bottom: 10px;
                    padding-left: 50px;
                    font-size: 24px;
                    color: #fff;
                    font-weight: 500;
                    line-height: 40px;

                    &:before {
                        content: "";
                        overflow: hidden;
                        position: absolute;
                        top: 0;
                        left: 0;
                        background-color: #232428;
                        background-position: 50% 50%;
                        background-repeat: no-repeat;

                        @include size(40px);
                        @include border-radius(100px);
                    }

                    &.ico-plug {
                        &:before {
                            background-position-y: 9px;
                            background-image: url(../images/comm/ico_plug.png);
                        }
                    }

                    &.ico-won {
                        &:before {
                            background-image: url(../images/comm/ico_won.png);
                        }
                    }

                    &.ico-elec {
                        &:before {
                            background-image: url(../images/comm/ico_elec.png);
                        }
                    }

                    &.ico-price {
                        &:before {
                            background-image: url(../images/comm/ico_price.png);
                        }
                    }
                }        
                
                .manage-group-in {
                    @include clear;

                    .manage-group-box,
                    .manage-group-chart {
                        float: left;
                        width: calc((100% - 25px) / 2);
                    }

                    .manage-group-box {
                        margin-right: 25px;
                    }
                }
            }

            .manage-group-board {
                padding: 20px 40px;
            }

            &.col2 {
                @include clear;

                .manage-group-item {
                    float: left;
                    width: calc((100% - 40px) / 2);
                    margin-left: 40px;

                    &:first-child {
                        margin-left: 0;
                    }
                }
            }

            &:first-child {
                margin-top: 0;
            }
        }
        
        .manage-box {
            margin-top: 20px;
            padding: 40px 40px 50px;
            background-color: #2b2c30;

            @include box-sizing;
            @include border-radius(5px);
            @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.75));

            .search-board {
                margin-bottom: 25px;
            }

            .paging {
                margin-top: 20px;
            }

            .manage-box-button {
                margin-top: 20px;
                padding: 0 30px;
                font-size: 0;
                text-align: right;

                .btn-default {
                    margin-right: 5px;
                    
                    &:last-child {
                        margin-right: 0;;
                    }
                }

                &.align-ctr {
                    text-align: center;
                }
            }
        }

        .manage-system-building {
            position: relative;

            .manage-system-building-top {
                position: absolute;
                top: -57px;
                font-size: 14px;
                color: #999;
                line-height: 32px;
                letter-spacing: 0;
                
                @include clear;

                li {
                    float: left;
                    margin-right: 35px;

                    em {
                        color: #fffaba;
                    }

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }

            .building-list {
                border-top: 1px solid #3a3c40;
                border-bottom: 1px solid #3a3c40;

                .building-item {
                    padding: 20px 90px;
                    border-bottom: 1px solid #3a3c40;

                    @include flexbox;
                    @include align-items(center);

                    .building-item-head {
                        width: 600px;

                        @include flexbox;
                        @include align-items(center);

                        .photo {
                            overflow: hidden;
                            display: block;
                            width: 140px;
                            // height: 92px;
                            margin-right: 50px;
                            border: 1px solid #131416;

                            @include border-radius(2px);

                            img {
                                max-width: 100%;
                                vertical-align: top;
                            }
                        }

                        .name,
                        .address {
                            display: block;
                            color: #fff;
                        }

                        .name {
                            font-size: 24px;
                            font-weight: 500;
                        }

                        .address {
                            margin-top: 20px;
                            font-size: 16px;
                        }
                    }

                    .building-item-owner {
                        width: 580px;
                    }
                    
                    .building-item-owner,
                    .building-item-info {
                        margin-left: 20px;
                        padding-top: 44px;
                        color: #fff;
                        letter-spacing: 0;

                        @include clear;

                        li {
                            position: relative;
                            float: left;
                            margin-left: 15px;
                            padding-left: 16px;

                            &:before {
                                content: "";
                                position: absolute;
                                top: 2px;
                                left: 0;
                                width: 1px;
                                height: 14px;
                                background-color: #666;
                            }

                            &:first-child {
                                margin-left: 0;
                                padding-left: 0;

                                &:before {
                                    display: none;
                                }
                            }
                        }
                    }

                    &:last-child {
                        border-bottom: 0;
                    }
                }
            }
        }

        .manage-system-alarm {
            .alarm-list {
                .alarm-list-row {
                    margin-top: 10px;
                    @include flexbox;
                    @include align-items(center);

                    .tit {
                        width: 370px;
                        min-height: 50px;
                        padding: 0 50px;
                        font-size: 16px;
                        color: #999;
                        background: #36373c;
                        background-image: -moz-linear-gradient( 90deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
                        background-image: -webkit-linear-gradient( 90deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
                        background-image: -ms-linear-gradient( 90deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
                        background-image: linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
                        text-align: right;
                        
                        @include flexbox;
                        @include align-items(center);
                        @include justify-content (flex-end);
                        @include border-radius(3px);
                        @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.2));
                        @include box-sizing;
                    }

                    .alarm-list-input {
                        min-height: 50px;
                        padding: 8px 40px;
                        border-top: 1px solid #3a3c40;
                        border-bottom: 1px solid #3a3c40;

                        @include flex;
                        @include box-sizing;

                        .input-text {
                            height: 32px;
                            padding: 0 10px;
                            border: 1px solid #48494e;
                            font-size: 16px;
                            color: #fff;
                            background-color: #1b1b1e;
                            vertical-align: middle;

                            @include border-radius(3px);
                            @include box-sizing;
                        }
                    }

                    .alarm-list-input-noti {
                        margin-left: 20px;
                        font-size: 14px;
                        color: #999;
                    }

                    &:first-child {
                        margin-top: 0;
                    }
                }
            }
        }
    }
}