@charset "utf-8";

// button
.btn-default {
    border: 1px solid #131416;
    font-size: 16px;
    color: #fff;
    background-color: #666;

    @include border-radius(5px);
    @include box-sizing;

    &.btn-color1 {
        background-color: #a57d28;
    }
}
a.btn-default {
    display: inline-block;
    vertical-align: top;

    &:hover {
        color: #fff;
    }
}

.btn-medium {
    min-width: 82px;
    padding: 0 25px;
    height: 42px;
}
a.btn-medium {
    line-height: 40px;
    text-align: center;
}
.btn-excel-dowm {
    height: 30px;
    padding: 0 12px;
    border: 1px solid #131416;
    font-size: 14px;
    color: #fff;
    font-weight: 500;
    background-color: #666;
    line-height: 1;

    @include border-radius(3px);

    span {
        display: inline-block;
        position: relative;
        height: 18px;
        padding-left: 22px;
        line-height: 18px;
        
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background: url(../images/comm/ico_excel.png) no-repeat 0 0;

            @include size(18px);
        }
    }
}

// form
.form-radio {
    display: inline-block;
    min-height: 20px;
    position: relative;
    vertical-align: middle;

    .form-radio-label {
        display: inline-block;
        position: relative;
        padding-left: 30px;
        font-size: 16px;
        color: #333;
        vertical-align: top;
        line-height: 1.2;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            background-color: #fff;

            @include size(20px);
            @include box-sizing;
            @include border-radius(50px);
        }

        &:after {
            content: "";
            display: none;
            position: absolute;
            left: 4px;
            top: 4px;
            background-color: #0547b4;

            @include size(12px);
            @include border-radius(50px);
        }
    }

    .form-radio-input {
        overflow: hidden;
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        font-size: 1%;
        z-index: -9999;
        clip: rect(1px, 1px, 1px, 1px);
        clip-path: inset(50%);
        opacity: 0;
        z-index: -9999;

        &:checked + .form-radio-label {
            &:after {
                display: block;
            }
        }
    }

    &.hide {
        .form-radio-label {
            padding-left: 0;

            @include size(20px);
        }
    }
}

.select-default {
    min-width: 100px;
    height: 32px;
    padding: 0 18px 0 8px;
    border: 1px solid #48494e;
    font-size: 16px;
    color: #fff;
    background-color: #1b1b1e;

    @include border-radius(3px);
    @include box-sizing;
}

.form-file {
    position: relative;
    font-size: 0;

    .form-file-box {
        overflow: hidden;
        display: inline-block;
        position: relative;
        vertical-align: top;
    }

    .input-file {
        position: absolute;
        top: -25%;
        right: -10%;
        bottom: -25%;
        left: -10%;
        width: 120%;
        height: 150%;
        opacity: 0;
        cursor: pointer;
    }

    .input-text {
        margin-right: 8px;
        vertical-align: middle;
    }

    .btn-form-file {
        width: 82px;
        height: 32px;
        border: 1px solid #131416;
        font-size: 16px;
        color: #fff;
        background-color: #666;
        vertical-align: middle;

        @include border-radius(3px);
    }
}

.textarea-default {
    width: 100%;
    padding: 15px 20px;
    border: 1px solid #48494e;
    font-size: 16px;
    color: #fff;
    background-color: #1b1b1e;
    line-height: 1.4;
    
    @include box-sizing;
    @include border-radius(5px);
}

.form-group {
    font-size: 0;

    .input-text {
        vertical-align: middle;

        & + .input-text {
            margin-left: 8px;
        }

        & + .select-default {
            margin-left: 8px;
            vertical-align: middle;
        }

        & + [class^=btn-] {
            margin-left: 8px;
            vertical-align: middle;
        }
    }

    .form-radio {
        & + .form-radio {
            margin-left: 30px;
        }
    }

    .select-default {
        vertical-align: middle;

        & + .select-default {
            margin-left: 8px;
        }

        & + [class^=btn-] {
            margin-left: 8px;
            vertical-align: middle;
        }
    }
}

// 게시판
.board {
    width: 100%;

    thead {
        @include border-radius(3px);
        @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.2));
    }
    
    th {
        height: 40px;
        font-size: 16px;
        color: #999;
        font-weight: 400;
        background-image: -moz-linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
        background-image: -webkit-linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
        background-image: -ms-linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
        background-image: linear-gradient(0deg,rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);

        &:first-child {
            border-radius: (3px 0 0 3px);
        }

        &:last-child {
            border-radius: (0 3px 3px 0);
        }
    }

    td {
        height: 40px;
        border-bottom: 1px solid #3a3c40;
        font-size: 16px;
        color: #fff;
        text-align: center;
    }

    .color1 {
        color: #ff9933;
    }
}
.board-medium {
    width: 100%;

    thead {
        @include border-radius(3px);
        @include box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.2));
    }
    
    th {
        height: 43px;
        font-size: 16px;
        color: #999;
        font-weight: 400;
        background-image: -moz-linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
        background-image: -webkit-linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
        background-image: -ms-linear-gradient(0deg, rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);
        background-image: linear-gradient(0deg,rgb(54,55,60) 0%, rgb(54,55,60) 96%, rgb(61,62,67) 96%, rgb(67,68,74) 100%);

        &:first-child {
            border-radius: (3px 0 0 3px);
        }

        &:last-child {
            border-radius: (0 3px 3px 0);
        }
    }

    td {
        height: 29px;
        padding: 10px;
        border-bottom: 1px solid #3a3c40;
        font-size: 16px;
        color: #fff;
        text-align: center;

        &.align-lft {
