        td {
            border-bottom-width: 2px;
            background-color: #303135;
            letter-spacing: 0;

            .total {
                color: #999;
                font-weight: 400;
            }

            .unit {
                color: #999;
            }
        }
    }

    .photo-small {
        display: inline-block;
        width: 100px;
        margin: 5px;
        border: 1px solid #131416;
        vertical-align: top;

        @include border-radius(2px);

        img {
            max-width: 100%;
            vertical-align: top;
        }
    }

    .btn-building-edit {
        display: inline-block;
        width: 72px;
        height: 32px;
        border: 1px solid #131416;
        font-size: 16px;
        color: #fff;
        background-color: #666;
        text-align: center;
        line-height: 30px;
        vertical-align: top;
        
        @include box-sizing;
        @include border-radius(3px);
    }
}
.board-view {
    width: 100%;

    th {
        padding: 23px 10px;
        border-bottom: 1px solid #232427;
        font-size: 16px;
        color: #999;
        font-weight: 400;
        background-color: #323339;
        line-height: 1.4;
    }

    td {
        padding: 15px 30px;
        border-bottom: 1px solid #232427;
        font-size: 16px;
        color: #fff;
        background-color: #2b2c30;
        line-height: 1.4;
    }

    .input-text {
        width: 100%;
        height: 42px;
        padding: 0 20px;
        border: 1px solid #48494e;
        font-size: 16px;
        color: #fff;
        background-color: #1b1b1e;

        @include box-sizing;
        @include border-radius(5px);
    }

    .form-file {
        .form-file-box {
            width: 100%;
        }

        .input-text {
            width: calc(100% - 92px - 8px);
        }

        .btn-form-file {
            width: 92px;
            height: 42px;

            @include border-radius(5px);
        }
    }

    .textarea-default {
        min-height: 300px;
    }

    tr {
        &:first-child {
            th,
            td {
                border-top: 1px solid #232427;
            }
        }
    }
}

// 게시판 검색
.search-board {
    padding: 0 20px;

    @include clear;

    .search-board-item {
        ul {
            @include clear;

            li {
                float: left;
                margin-right: 60px;

                @include flexbox;
                @include align-items(center);

                .tit {
                    margin-right: 12px;
                    font-size: 16px;
                    color: #fffaba;
                    font-weight: 400;
                }

                &:last-child {
                    margin-right: 0;
                }
            }
        }

        .form-radio {
            .form-radio-label {
                color: #fff;
            }
        }

        .input-text {
            height: 32px;
            padding: 0 10px;
            border: 1px solid #48494e;
            font-size: 16px;
            color: #fff;
            background-color: #1b1b1e;

            @include border-radius(3px);
            @include box-sizing;

            &.align-ctr {
                text-align: center;
            }
        }

        .wave {
            width: 38px;
            font-size: 16px;
            color: #fff;
            text-align: center;
        }

        .form-group {
            height: 20px;
            padding: 6px 0;
        }
    }
    
    .search-board-lft {
        float: left;
    }

    .search-board-rgt {
        float: right;

        @include clear;

        ul {
            float: left;
        }

        .btn-search {
            float: left;
            width: 72px;
            height: 32px;
            margin-left: 5px;
            border: 1px solid #131416;
            font-size: 16px;
            color: #fff;
            background-color: #666;

            @include border-radius(3px);
        }
    }
}

// 차트 검색
.search-chart {
    padding: 25px;

    .search-chart-wrap {
        @include flexbox;
        @include justify-content(center);
    }

    .search-chart-item {
        @include flexbox;
        @include align-items(center);

        .label {
            margin-right: 12px;
            font-size: 16px;
            color: #fffaba;
            font-weight: 400;
        }

        .form-radio {
            .form-radio-label {
                color: #fff;
            }
        }

        .input-text {
            height: 32px;
            padding: 0 10px;
            border: 1px solid #48494e;
            font-size: 16px;
            color: #fff;
            background-color: #1b1b1e;

            @include border-radius(3px);
            @include box-sizing;

            &.align-ctr {
                text-align: center;
            }
        }

        .wave {
            width: 38px;
            font-size: 16px;
            color: #fff;
            text-align: center;
        }

        .btn-search {
            width: 72px;
            height: 32px;
            margin-left: 5px;
            border: 1px solid #131416;
            font-size: 16px;
            color: #fff;
            background-color: #666;

            @include border-radius(3px);
        }

        &.search-chart-date {
            margin: 0 80px 0 110px;
        }
    }
}

.chart-list {
    overflow: hidden;
    padding: 50px 185px 90px;

    .swiper {
        overflow: visible;
    }

    .swiper-wrapper {
        @include clear;
    }

    .chart-list-item {
        overflow: hidden;
        float: left;
        width: 240px;
        height: 260px;
        margin-right: 50px;
        border: 1px solid #131418;
        background: #212124 url(../images/bg_chart_list_item.jpg) no-repeat 50% 50%;;
        cursor: pointer;
        
        @include box-sizing;
        @include border-radius(5px);
        @include transition(border .3s);
        
        &:hover {
            border-color: #666;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    .swiper-slide-active {
        border-color: #666;
    }

    .btn-swiper-prev,
    .btn-swiper-next {
        width: 30px;
        height: 58px;
        margin-top: -29px;
        border: 0;

        &:before,
        &:after {
            display: none;
        }
    }
    
    .btn-swiper-prev {
        left: -80px;
        background: url(../images/btn_chart_list_prev.png) no-repeat 0 0;
    }

    .btn-swiper-next {
        right: -80px;
        background: url(../images/btn_chart_list_next.png) no-repeat 0 0;
    }


    .swiper-scrollbar {
        height: 8px;
        bottom: -48px;
        background-color: #1f2023;
        cursor: pointer;
    }
    .swiper-horizontal {
        > .swiper-scrollbar {
            left: 0;
            width: 100%;
        }
    }

    .swiper-scrollbar-drag {
        background-color: #666;
    }
}

// paging
.paging {
    font-size: 0;
    text-align: center;

    .btn-paging {
        display: inline-block;
        margin-left: 5px;
        font-size: 16px;
        color: #999;
        vertical-align: middle;
        text-align: center;
        line-height: 40px;

        @include size(40px);
        @include box-sizing;
        @include border-radius(100px);

        &:first-child {
            margin-left: 5px;
        }

        &.is-active {
            color: #fffaba;
            background-color: #26272a; 
        }
    }

    .btn-paging-quick {
        display: inline-block;
        font-size: 16px;
        vertical-align: middle;

        @include size(40px);
        @include box-sizing;

        &.btn-paging-first {
            margin-right: 5px;
            background: url(../images/comm/btn_paging_first.png) no-repeat 50% 50%;
        }

        &.btn-paging-prev {
            margin-right: 15px;
            background: url(../images/comm/btn_paging_prev.png) no-repeat 50% 50%;
        }

        &.btn-paging-next {
            margin-left: 15px;
            background: url(../images/comm/btn_paging_next.png) no-repeat 50% 50%;
        }

        &.btn-paging-last {
            margin-left: 5px;
            background: url(../images/comm/btn_paging_last.png) no-repeat 50% 50%;
        }
    }
}

// popup
.pop-layer {
    position: absolute;
    top: calc(50% - 120px);
    left: 50%;
    padding: 18px 18px 50px;
    border: 1px solid #151516;
    background-color: #2e2f34;
    z-index: 9999;

    @include box-sizing;
    @include border-radius(20px);
    @include box-shadow(0 5px 30px 0 rgba(0, 0, 0, 0.4));
    @include translate(0, -50%);

    .pop-head {
        position: relative;
        padding: 12px 20px;
        border: 1px solid #232427;
        font-size: 24px;
        color: #fef698;
        font-weight: 500;
        background-color: #35363e;

        @include border-radius(10px);
        @include box-sizing;

        .btn-check-number {
            position: absolute;
            top: 50%;
            right: 60px;
            width: 112px;
            height: 32px;
            margin-top: -16px;
            border: 1px solid #131416;
            font-size: 16px;
            color: #fff;
            background-color: #666;
            vertical-align: middle;

            @include border-radius(3px);
        }
    }

    .pop-body {
        margin-top: 30px;
    }

    .board-pop-input {
        width: 100%;
        
        th {
            padding: 0 30px;
            border-bottom: 1px solid #232427;
            font-size: 16px;
            color: #999;
            font-weight: 400;
            background-color: #323339;
            text-align: right;
        }

        td {
            padding: 9px 20px;
            border-bottom: 1px solid #232427;
            color: #fff;
            background-color: #2e2f34;
        }

        tr {
            &:first-child {
                th,
                td {
                    border-top: 1px solid #232427;
                }
            }
        }

        .form-radio {
            .form-radio-label {
                color: #fff;
            }
        }

        .btn-dup {
            width: 82px;
            height: 32px;
            border: 1px solid #131416;
            font-size: 16px;
            color: #fff;
            background-color: #666;
            vertical-align: middle;

            @include border-radius(3px);
        }

        .btn-facility-reg {
            width: 82px;
            height: 32px;
            border: 1px solid #131416;
            font-size: 16px;
            color: #fff;
            background-color: #a57d28;
            vertical-align: middle;

            @include border-radius(3px);
        }
    }

    .board-pop-button {
        margin-top: 20px;
        font-size: 0;
        text-align: center;

        .btn-default {
            margin-right: 5px;
            
            &:last-child {
                margin-right: 0;
            }
        }
    }

    .ico-required {
        color: #fef698;
    }
    
    .input-text {
        width: calc(100% - 30px);
        height: 32px;
        padding: 0 10px;
        border: 1px solid #48494e;
        font-size: 16px;
        color: #fff;
        background-color: #1b1b1e;

        @include border-radius(3px);
        @include box-sizing;
    }

    .btn-pop-clse {
        position: absolute;
        top: 23px;
        right: 29px;
        border: 0;
        background: url(../images/comm/ico_pop_clse.png) no-repeat 50% 50%;

        @include size(40px);
    }
}

