// 시설물 등록
.pop-facility-reg {
    width: 660px;
    margin-left: -330px;

    .facility-wrap {
        position: relative;
        padding-bottom: 35px;
        border-bottom: 1px solid #232427;

        @include clear;

        .tit {
            display: block;
            margin-bottom: 15px;
            font-size: 20px;
            color: #999;
            font-weight: 400;
            text-align: center;
        }

        .facility-input {
            float: left;
            width: 240px;
            height: 350px;
            padding: 25px 19px;
            background-color: #323339;
            text-align: center;

            @include box-sizing;

            .facility-input-box {
                height: 270px;

                @include flexbox;
                @include align-items(center);
            }

            .input-text {
                width: 100%;
            }
        }

        .facility-button {
            position: absolute;
            top: 0;
            left: 50%;
            height: 350px;
            padding: 60px 0 25px;

            @include translate(-50%, 0);
            @include box-sizing;
            @include flexbox;
            @include flex-direction(column);
            @include justify-content(center);

            .btn-facility-add,
            .btn-facility-del {
                display: block;
                width: 82px;
                height: 32px;
                border: 1px solid #131416;
                font-size: 16px;
                color: #fff;

                @include box-sizing;
                @include border-radius(3px);
            }

            .btn-facility-add {
                background-color: #a57d28;

                span {
                    position: relative;
                    padding-right: 11px;

                    &:after {
                        content: "";
                        position: absolute;
                        top: 50%;
                        right: 0;
                        width: 8px;
                        height: 13px;
                        margin-top: -6px;
                        background: url(../images/comm/ico_gt_small.png) no-repeat 0 0;
                    }
                }
            }

            .btn-facility-del {
                margin-top: 5px;
                background-color: #666;

                span {
                    position: relative;
                    padding-left: 11px;

                    &:before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 0;
                        width: 8px;
                        height: 13px;
                        margin-top: -6px;
                        background: url(../images/comm/ico_lt_small.png) no-repeat 0 0;
                    }
                }
            }
        }

        .facility-list {
            float: right;
            width: 240px;
            height: 350px;
            padding: 25px 19px;
            background-color: #323339;

            @include box-sizing;

            ul {
                overflow-y: auto;
                height: 270px;
                padding: 20px;
                border: 1px solid #48494e;
                background-color: #1b1b1e;

                @include box-sizing;
                @include border-radius(3px);

                li {
                    margin-top: 15px;
                    font-size: 16px;
                    color: #fff;
                    cursor: pointer;

                    &:first-child {
                        margin-top: 0;
                    }
                }
            }
        }
    }
}